
import { defineComponent } from 'vue'
import Canvas from 'canvas'

export default defineComponent({
    data() {
        return {
            art: ''
        }
    },

    methods: {
        async convert() {
            const density = '$@B%8&WM#*oahkbdpqwmZO0QLCJUYXzcvunxrjft/\\|()1{}[]?-_+~<>i!lI;:,"^`\'. '.split('')
            console.log(density)

            const img = await Canvas.loadImage('./img/img.7aa64a17.png')
            const canvas = Canvas.createCanvas(img.width, img.height)
            const ctx = canvas.getContext('2d')
            ctx.drawImage(img, 0, 0)
            const imgData = ctx.getImageData(0, 0, img.width, img. height).data

            for(let i = 0; i < imgData.length; i+=4) {
                const r = imgData[i]
                const g = imgData[i + 1]
                const b = imgData[i + 2]
                const a = imgData[i + 3]

                let brightness = Math.floor((r + g + b) / 3)
                if(a == 0) brightness = 255

                const char = density[Math.floor(density.length / 256 * brightness)]
                //console.log(brightness, Math.floor(density.length / 256 * brightness))

                if(( (i / 4) % img.width ) == 0) this.art += '\n'
                console.log((i / 4) % img.width)
                this.art += ( char == ' ' ? '\xa0' : char )
            }
        }
    }
})
